export const allTalents = {
    'Air of Authority': { name: "Air of Authority", tier: 1, prerequisites: "Fel 30", devotion: EGods.SLAANESH, description: "When making Command Skill Tests, the character may affect a number of targets equal to 10 times his Fellowship bonus. If the character has Minions (see page 135) they are also more devoted to him and add +10 to their Loyalty when in his presence. This talent has no effect on hostile targets, and only affects NPCs." },
    'Ambidextrous': { name: "Ambidextrous", tier: 1, prerequisites: "Ag 30", devotion: EGods.UNALIGNED, description: "The character may use either hand equally well for any task, and does not suffer the –20 penalty for actions using his off hand. Special: When combined with the Two-Weapon Wielder Talent, the penalty for making attacks with both weapons in the same Turn drops to –10." },
    'Ancient Warrior': { name: "Ancient Warrior", tier: 1, devotion: EGods.UNALIGNED, description: "This Talent must be taken at Character Creation. The character can command special respect from other veterans of the Long War and gains a +10 to all Fellowship Tests and Skill Tests based on Fellowship when dealing with such NPCs. In addition, Chaos Space Marine Minions of the character increase their loyalty by +10. Long War weaponry is also easier for him to obtain and all weapons with the word “Legion” in the title count as one Availability category easier than normal (i.e. Rare becomes Scarce, Scarce becomes Average, etc.). Finally, veterans of the Long War are despised by the Imperium and the Adeptus Astartes especially, and should the character’s past become known by these powers it is likely to end in blood…" },
    "Armour-Monger": { name: "Armour-Monger", tier: 2, prerequisites: "Int 35, Tech Use, Trade (Armorer)", devotion: EGods.UNALIGNED, description: "The character increases the Armour Points of any armour he wears by 2 on all locations it would normally cover as long as he has at least an hour each day to clean and repair it or make minor modiﬁ cations. This bonus applies only to armour worn by the character as it combines his training as much as his skill at armoury." },
    "Arms Master": { name: "Arms Master", tier: 3, prerequisites: "WS 40, BS 40, Weapon Training (any two)", devotion: EGods.UNALIGNED, description: "When the Heretic uses a weapon he is untrained in, he only suffers a –10 penalty, rather than a –20. The only exceptions to the Arms Master Talent are exotic weapons, which are so unusual or rare that they always require their own weapon training Talents to use without penalty and gain no bonus when combined with this talent." },
    "Assassin Strike": { name: "Assassin Strike", tier: 3, prerequisites: "Ag 40, Acrobatics", devotion: EGods.SLAANESH, description: "After making a melee attack, a successful Acrobatics Skill Test allows the character to move at half rate as a Free Action. The character’s opponent does not receive a free attack resulting from this move. The character may only make this move once per round." },
    "Baleful Dirge": { name: "Baleful Dirge", tier: 2, prerequisites: "Disturbing Voice, Intimidate +10", devotion: EGods.NURGLE, description: "Singing the dirge takes some concentration and requires a Half Action each round to maintain. All creatures which can hear the character within a 30-metre radius must make a Willpower Test or suffer a –10 on their next roll. As long as the dirge is maintained, all those affected by it must test each turn to see if they suffer its effects. Once a creature passes a Willpower Test however, it has completely overcome the dirge and is not affected by it for the remainder of the encounter." },
    "Bastion of Iron Will": { name: "Bastion of Iron Will", tier: 3, prerequisites: "Psy Rating, Strong Minded, WP 40", devotion: EGods.TZEENTCH, description: "The character adds 5 x his Psy Rating on any Opposed Test involving defending against the Psyniscience Skill or Psychic Powers." },
    "Battle Rage": { name: "Battle Rage", tier: 2, prerequisites: "Frenzy", devotion: EGods.KHORNE, description: "The character may Parry while frenzied. In addition such is the character’s control he may reroll a failed roll to snap out of Frenzy or resist entering Frenzy if he so chooses." },
    "Berserk Charge": { name: "Berserk Charge", tier: 1, devotion: EGods.KHORNE, description: "If the character uses the Charge Action, he gains a +30 bonus to Weapon Skill instead of +20." },
    "Betrayer": { name: "Betrayer", tier: 2, prerequisites: "Cold Hearted, Intimidate", devotion: EGods.UNALIGNED, description: "Once per game session the character may kill either an allied NPC (i.e. an NPC which is openly friendly toward the PC and trusts him) or one of his own Minions. Such calculated cruelty boosts the Heretic’s reputation, granting him a +10 bonus on his next Infamy Test if the victim was an NPC and +20 if it was a Minion (and harder to replace). Killing NPCs and Minions usually has consequences as well which should be determined by the GM." },
    "Blade Dancer": { name: "Blade Dancer", tier: 3, prerequisites: "WS 40, Two-Weapon Wielder (Melee)", devotion: EGods.UNALIGNED, description: "When attacking with any bladed weapon, including chainswords, axes, and powerswords, the character may reroll one missed attack per round." },
    "Blademaster": { name: "Blademaster", tier: 3, prerequisites: "WS 30, Weapon Training (Any melee)", devotion: EGods.KHORNE, description: "When armed with two singled handed melee weapons with the Balanced Quality, the character reduces the penalties for Two-Weapon Fighting (see page 243) by 10 (so –20 would drop to –10). If taken with the Ambidextrous Talent, this drops the penalty to +0." },
    "Blasphemous Incantation": { name: "Blasphemous Incantation", tier: 3, prerequisites: "Psy Rating", devotion: EGods.TZEENTCH, description: "Using a power with an incantation increases the Focus Power Test by a Half Action, as the psyker requires a few moments to speak the required words. Powers for which the Focus Power Test takes a Reaction cannot be used with an invocation. Using a power with an invocation grants the psyker a +1 bonus to his Psy Rating, after determining Psychic Strength. This bonus to PR does not inﬂuence the Psychic Strength in any way; their beneﬁt is applied after determining whether a power is used at the Unfettered, Fettered or Push level. However, the psyker must add +20 to any roll on the Psychic Phenomena table. Finally, any result of 91 or higher on the Focus Power Test is an automatic failure that results in Perils of the Warp." },
    "Blind Fighting": { name: "Blind Fighting", tier: 1, prerequisites: "Per 30", devotion: EGods.TZEENTCH, description: "This talent reduces all penalties for ﬁghting with a melee weapon while suffering from obscured vision by half (rounded up), permitting the character to ﬁght in fog, smoke or darkness more effectively. See page 245 for a full list of attack penalties based on lighting and vision. Note this talent only improves a character’s chances to hit with melee weapons and has not effect on ranged weapon attacks." },
    "Blood God's Contempt": { name: "Blood God's Contempt", tier: 3, prerequisites: "Frenzy, WP 40", devotion: EGods.KHORNE, description: "The character spends the time before battle armouring himself against the power of the warp by cursing the names of sorcerers and showing his fearlessness before his feeble power and all those who wield the power of the warp. This talent may be used at any time by a character as long as he has at least an hour to perform his ritual of contempt (a player may say his character has already performed the ritual before play begins if the GM agrees). The character must also spend one Infamy Point. During the game session whenever the character is the subject of the effects of a psychic power, daemonic power or other warp based power, he may make a Willpower Test (make this test before any tests he would normally be permitted to resist the effects of the power). If the Willpower test is passed the character is unaffected by the psychic power. If the test is failed then the psychic power affects him as normal and he becomes Frenzied in addition to any other effects. The effects of this talent only apply to powers that directly affect the character (such as targeted powers or powers which catch him in their area of effect) and not powers which may harm him indirectly (such as attack from summoned daemons or falling or flung debris from a telekinetic). In addition, powers used by friendly psykers must also be resisted (and also cause Frenzy should he fail to resist them). A power that is resisted by a character using this talent is not nullified an others are affected by it as normal." },
    "Bolter Drill": { name: "Bolter Drill", tier: 3, prerequisites: "Weapon Training (Bolt), BS 40", devotion: EGods.UNALIGNED, description: "When ﬁring any class of Bolt weapon on full or semi-auto the character may score one additional Degree of Success (provided the Test is successful) if he does not take a Move Action in his turn." },
    "Bugling Biceps	": { name: "Bugling Biceps", tier: 2, prerequisites: "S 45", devotion: EGods.NURGLE, description: "The character can ﬁre heavy weapons using Semi-Auto Burst or Full Auto Burst without bracing, and he does not suffer the -30 penalty for failing to brace. In addition whenever the character uses the Athletics Heft Special Skill Use (see page 96) he may add +20 to his Athletics Skill Test to reﬂect his massive muscles." },
    "Catfall": { name: "Catfall", tier: 1, devotion: EGods.SLAANESH, description: "The character automatically reduces the distance of all falls by a number of metres equal to his Agility Bonus, ignoring this distance as if it did not exist. In addition the character adds +20 to his Acrobatics Skill Tests when using the Jump Special Skill Use as it pertains to reducing Damage from falling." },
    "Child of the Warp": { name: "Child of the Warp", tier: 2, prerequisites: "Psy Rating", devotion: EGods.UNALIGNED, description: "This grants greater connection to the warp and increases the character’s Psy Rating by +1 when he chooses to Push . The downside, however, is that the warp is always partially manifest in his presence and he generates constant psychic phenomena. At the start of each game session the character should roll 1d10 on Table 4-4: Child of the Warp to determine the nature of these persistent effects which follow him for the duration of the session." },
    "Cold Hearted": { name: "Cold Hearted", tier: 1, devotion: EGods.UNALIGNED, description: "Seduction attempts against him automatically fail, and he gains a +20 to his Willpower when making Opposed Tests against an opponent using the Charm Skill. Taking this Talent gives the character one Corruption Point." },
    "Combat Formation": { name: "Combat Formation", tier: 1, prerequisites: "Int 40", devotion: EGods.TZEENTCH, description: "Before rolling initiative, all members of the group (including the character) add +1 to his Initiative Rolls. Any member of the group may also choose to use the Intelligence Bonus of the character for their initiative roll rather than their own Agility Bonus." },
    "Combat Master": { name: "Combat Master", tier: 2, prerequisites: "WS 30", devotion: EGods.KHORNE, description: "Opponents ﬁghting the character in hand-to-hand combat gain no bonuses for Ganging Up (see page 245)." },
    "Combat Sense": { name: "Combat Sense", tier: 1, prerequisites: "Per 30", devotion: EGods.UNALIGNED, description: "The character may use his Perception Bonus in place of his Agility Bonus when rolling Initiative." },
    "Corpus Conversion": { name: "Corpus Conversion", tier: 2, prerequisites: "Psy Rating, Human", devotion: EGods.NURGLE, description: "A psyker with the Corpus Conversion Talent gains access to this Path to Power, and may elect to use it before he attempts his Focus Power Test. The psyker may gain a number of bonus Degrees of Success no greater than his Toughness Bonus on his Focus Power Test. However, for every Degree of Success gained in this way, the psyker suffers 1d10 Toughness Damage (note, this is not permanent Toughness Damage) and one Corruption Point as the energies of the warp poison his body from within. Traitor Legion characters may not beneﬁt from Corpus Conversion; the taint-resistant metabolism of the Astartes makes them incapable of burning away his ﬂesh to fuel warp-based powers." },
    "Counter Attack": { name: "Counter Attack", tier: 2, prerequisites: "WS 40", devotion: EGods.UNALIGNED, description: "After successfully parrying an opponent’s attack, the character may immediately make an attack against that opponent using the weapon with which he parried as a Free Action even though it is not his turn. This attack suffers a –20 penalty and may not be combined with any ability which allows more than one hit or attack such as Lighting Attack or Two-Weapon Fighting." },
    "Crack Shot": { name: "Crack Shot", tier: 2, prerequisites: "BS 50", devotion: EGods.TZEENTCH, description: "When the character’s ranged attack causes Critical Damage, add +2 to the Damage result." },
    "Crippling Strike": { name: "Crippling Strike", tier: 2, prerequisites: "WS 50", devotion: EGods.SLAANESH, description: "When the character’s melee attack causes Critical Damage add +2 to the Damage result." },
    "Crushing Blow": { name: "Crushing Blow", tier: 3, prerequisites: "WS 40", devotion: EGods.KHORNE, description: "The character adds half his Weapon Skill Bonus to Damage he inﬂicts in melee." },
    "Cursed Heirloom": { name: "Cursed Heirloom", tier: 1, devotion: EGods.UNALIGNED, description: "Passed down through the ages, the character has come into possession of an ancient ancestral heirloom. He must roll a 1d5 on the Cursed Heirloom Table for any weapon or armour giving it a permanent curse. He is now bonded to the item and even if it is lost, destroyed, or stolen it always somehow turns up in its original condition at the start of the next game session. Such items also usually come with a history of their own and the GM should feel free to have the item’s past come back to haunt the character from time to time" },
    "Deadeye Shot": { name: "Deadeye Shot", tier: 1, prerequisites: "BS 30", devotion: EGods.TZEENTCH, description: "When making a Called Shot (see page 234) ranged attack, the character reduces the penalty by 10 (so a –20 becomes a –10 instead)." },
    "Deflect Shot": { name: "Deflect Shot", tier: 2, prerequisites: "Ag 50", devotion: EGods.UNALIGNED, description: "When the character is armed with a readied melee weapon they add his Weapon Skill Bonus to his armour points on all locations (even unarmed locations) when calculating Damage from hits from ranged weapons with the Primitive Special Quality or Thrown weapons." },
    "Demagogue": { name: "Demagogue", tier: 3, prerequisites: "Inspire Wrath", devotion: EGods.SLAANESH, description: "When the character uses the Charm or Intimidate Skill to affect more than one person they may affect up to a 100 times his Fellowship Bonus. In addition such is the character’s natural magnetism and charisma that he adds +10 to the loyalty of all his Minions." },
    "Die Hard": { name: "Die Hard", tier: 1, prerequisites: "WP 40", devotion: EGods.NURGLE, description: "When he suffers from blood loss, he may roll twice to avoid death." },
    "Disarm": { name: "Disarm", tier: 1, prerequisites: "Ag 30", devotion: EGods.UNALIGNED, description: "If in close combat, the character may use a Full Action to disarm his foe by making an Opposed Weapon Skill Test. If the character wins the test, the enemy drops his weapon at his feet. Should the character obtain three or more Degrees of Success, he may take the enemy’s weapon from him." },
    "Disturbing Voice": { name: "Disturbing Voice", tier: 1, devotion: EGods.NURGLE, description: "The character gains a +10 bonus to all Intimidate Skill Tests, but suffers a –10 penalty to Fellowship Tests when dealing with others in a non-threatening manner, such as animals, children or the easily startled. In addition, the character is more adept at instilling fear in his followers and when using the Special Skill Use Terrify (see page 97), and adds +10 to his Command Skill Test to counter the effects of Fear." },
    "Double Team": { name: "Double Team", tier: 1, devotion: EGods.UNALIGNED, description: "When ganging up on an opponent, the character gains an additional +10 bonus to Weapon Skill Tests. If one or more of the other allies ganging up on the opponent have this Talent, both gain an additional +10 bonus, for a total of +20. This bonus is in addition to the normal bonus gained from Ganging Up on an opponent (see page 245)." },
    "Electro Graft Use": { name: "Electro Graft Use", tier: 1, prerequisites: "Mechanicus Implants", devotion: EGods.UNALIGNED, description: "Grants and additional +10 to common lore, inquiry, or Tech-Use whilst connected to a data Port." },
    "Enemy": { name: "Enemy", tier: 1, devotion: EGods.UNALIGNED, description: "The character suffers an additional –10 multiplied X times penalty to Fellowship Tests when dealing with this group and the GM may use them to complicate his life from time to time. Unlike other talents this talent does not cost any experience points to purchase and cannot be taken as an advance." },
    "Excessive Wealth": { name: "Excessive Wealth", tier: 1, prerequisites: "Inf 40", devotion: EGods.SLAANESH, description: "The character gains a +10 bonus to his Infamy whenever he makes an Acquisition Test representing his increased ability to buy goods." },
    "Exotic Weapon Training": { name: "Exotic Weapon Training", tier: 2, devotion: EGods.UNALIGNED, description: "The character is trained to use a particularly obscure type of weapon, allowing him to use armaments few others have ever seen. Weapons requiring this unique talent are noted in their armoury descriptions. When a character attempts to use a weapon he does not have the correct Weapon Training Talent for, he suffers a –20 penalty to any relevant Weapon Skill or Ballistic Skill Test. Unlike other kinds of weapons training this talent applies only to a single weapon and not a class of weapons" },
    "Eye of Vengeance": { name: "Eye of Vengeance", tier: 3, prerequisites: "BS 50", devotion: EGods.TZEENTCH, description: "Before making a Standard Attack ranged attack action, the character may spend an Infamy Point. If he does so, his attack deals +X Damage and gains +X Pen, where X equals the number of Degrees of Success scored on the attack roll." },
    "Favoured by the Warp": { name: "Favoured by the Warp", tier: 3, prerequisites: "WP 35", devotion: EGods.TZEENTCH, description: "Whenever a Power Roll results in Psychic Phenomena, the character may roll one additional time on that table and take the more favourable result. Perils of the Warp affect him as normal—such is the capacious nature of the gods that not even the “favoured” can avoid their gaze from time to time…" },
    "Fearless": { name: "Fearless", tier: 3, prerequisites: "Nerves of Steel", devotion: EGods.NURGLE, description: "The character is immune to the effects of Fear and Pinning, but disengaging from combat or backing down from a ﬁght is difﬁcult for him to stomach and as such requires a successful Willpower Test." },
    "Ferric Summons": { name: "Ferric Summons", tier: 1, prerequisites: "Ferric Lure Implants", devotion: EGods.UNALIGNED, description: "The Heretic can call an unsecured metal object to his hand as with Ferric Lure Implants, but may summon objects of up to 2 kilograms per point of his Willpower Bonus and may summon objects up to 40 metres distant." },
    "Flesh Render": { name: "Flesh Render", tier: 1, prerequisites: "Weapon Training (Chain)", devotion: EGods.KHORNE, description: "When inﬂicting Damage with a melee weapon that has the Tearing Quality, the character rolls two additional die for Damage, rather than the normal one for using a Tearing weapon, and discards the lowest two Damage dice results." },
    "Foresight": { name: "Foresight", tier: 2, prerequisites: "Int 30", devotion: EGods.TZEENTCH, description: "By spending ten minutes studying or analysing a problem, the character gains a +10 bonus to his next Intelligence Test or a Skill Test based on Intelligence." },
    "Frenzy": { name: "Frenzy", tier: 1, devotion: EGods.KHORNE, description: "If the character spend one full round fuelling anger (through flagellation, drugs, or other means), next round go into an uncontrolled rage, gaining a +10 bonus to Weapon Skill, Strength, Toughness, and Willpower, but suffering a –20 penalty to Ballistic Skill, Intelligence and Fellowship. The character must attack the nearest enemy in melee combat if possible. While Frenzied, he is immune to Fear, Pinning, stunning effects, the effects of Fatigue, and he may not Parry, retreat, or flee. He remains Frenzied for the duration of the combat. Characters may not use psychic powers whilst in Frenzy. After combat ends, or if there are no more eligible targets for the character to attack he can make a Willpower Test to snap out of his Frenzy. If he fails he must continue to attack, favouring NPCs over PCs. Each successive round however he may make another Willpower Test, with a cumulative +10 Bonus to return to a stable state of mind and come out of Frenzy." },
    "Furious Assault": { name: "Furious Assault", tier: 2, prerequisites: "WS 35", devotion: EGods.KHORNE, description: "If the character successfully hits his target using the All Out Attack Action, he may spend his Reaction (and thus not being able to parry or dodge until his next turn) to make an additional attack using the same bonuses or penalties as the original attack." },
    "Greater Minion of Chaos": { name: "Greater Minion of Chaos", tier: 3, prerequisites: "Fel 55, Infamy 50", devotion: EGods.UNALIGNED, description: "Details on creating a Greater Minion can be found on page 135. This talent may be taken more than once, each time adding another Greater Minion to the character’s retinue." },
    "Gunslinger": { name: "Gunslinger", tier: 3, prerequisites: "BS 40, Two-Weapon Wielder (Ballistic)", devotion: EGods.UNALIGNED, description: "When armed with two pistol-class weapons, both of which he can use single-handedly, the character reduces the penalty for Two-Weapon Fighting by 10 (so –20 would drop to –10). If taken with the Ambidextrous Talent, this drops the penalty to +0." },
    "Hammer Blow": { name: "Hammer Blow", tier: 3, prerequisites: "Crushing Blow", devotion: EGods.KHORNE, description: "When the character makes an All Out Attack manoeuvre to make a single melee attack he may add half his Strength Bonus to the weapon’s Armour Penetration. The attack also counts as having the Concussive (2) Special Quality to represent the shocking force of the blow’s impact." },
    "Hard Target": { name: "Hard Target", tier: 2, prerequisites: "Ag 50", devotion: EGods.SLAANESH, description: "When the character performs a Charge or Run action, opponents suffer a –20 penalty to Ballistic Skill Tests made to hit the character with a ranged weapon. This penalty continues until the start of the character’s next turn." },
    "Hardy": { name: "Hardy", tier: 2, prerequisites: "T 40", devotion: EGods.NURGLE, description: "When undergoing medical treatment or healing from injures , the character always recovers Damage as if lightly wounded regardless of the level of Damage he has sustained." },
    "Hatred": { name: "Hatred", tier: 2, devotion: EGods.KHORNE, description: "A group, organisation or race has wronged the character in the past, fuelling this animosity. When fi ghting opponents of that group in close combat, the character gains a +10 bonus to all Weapon Skill Tests made against him. The character also finds it difficult to back down from a fight with his hated foe and must make a Willpower Test to retreat or surrender when fighting him unless massively outnumbered or outclassed" },
    "Heightened Senses": { name: "Heightened Senses", tier: 1, devotion: EGods.TZEENTCH, description: "When the character gains this Talent, select one of the ﬁve senses: The character gains a +10 bonus to any tests speciﬁcally involving this sense. Thus, Heightened Senses (Sight) would apply to an Awareness Test to see a ﬂock of Shale Crows, but not to a Ballistic Skill Test simply because the character is using his eyes. This talent may be taken more than once, each time with a different specialisation." },
    "Hip Shooting": { name: "Hip Shooting", tier: 2, prerequisites: "BS 40, Ag 40", devotion: EGods.UNALIGNED, description: "As a Full Action, the character may both move up to his Full Move rate and make a single attack with a ranged weapon. This attack can only be a single shot and may not be a semi or full automatic ranged attack. Characters with the Two-Weapon Fighting talent may use this talent with Hip Shooting to make two single shots if he is armed with a ranged weapon in either hand." },
    "Hotshot Pilot": { name: "Hotshot Pilot", tier: 2, prerequisites: "Ag 40, Operate (any)", devotion: EGods.UNALIGNED, description: "The character counts as having all Operate Skills (essentially, he can drive or ﬂy anything) at Rank 1 (Known). If he wishs to later improve his Operate Skills they must be bought using experience points (from Rank 1) as normal. Such is his skill he also adds one Degree of Success to any successful Operate Tests." },
    "Independent Targeting": { name: "Independent Targeting", tier: 2, prerequisites: "BS 40", devotion: EGods.UNALIGNED, description: "When ﬁring two weapons as part of a single action (using the Two-Weapon Fighting talent), the targets need not be less than 10 metres apart." },
    "Infused Knowledge": { name: "Infused Knowledge", tier: 3, prerequisites: "Int 40, Lore (any)", devotion: EGods.TZEENTCH, description: "The character counts as having all Common Lore and Scholastic Lore (essentially, he knows something about everything) at Rank 1 (Known). If he wishes to later improve his Lore Skills they must be bought using experience points (from Rank 1) as normal. Such is his skill that he also adds one Degree of Success to any successful Common or Scholastic Lore Tests." },
    "Inspire Wrath": { name: "Inspire Wrath", tier: 2, prerequisites: "Air of Authority", devotion: EGods.SLAANESH, description: "As a Full Action, the character can make a Charm Test to grant the effects of the Hatred Talent to any people in his immediate vicinity. Success on the test grants the Hatred Talent to the listeners (with the object of hate chosen by the character) for the duration of an encounter. Listeners can resist the effects of the characters speech (and therefore not be burdened with his chosen hatred) by passing an Opposed Willpower Test against the character’s Charm Skill Test. Inspire Wrath affects up to 10 times the character’s Fellowship, which can be increased with the use of the Demagogue Talent as normal. Both players and GMs should bear in mind, however, that once a crowd has been inspired to hatred or mindless anger the effects can be unpredictable…" },
    "Into the Jaws of Hell": { name: "Into the Jaws of Hell", tier: 3, prerequisites: "Iron Discipline, Fel 50", devotion: EGods.SLAANESH, description: "His followers become immune to the effects of Fear and Pinning as long as he is physically there to lead them and they can see him leading by example. Heretics cannot beneﬁt from the Into the Jaws of Hell of another Heretic." },
    "Iron Jaw": { name: "Iron Jaw", tier: 1, prerequisites: "T 40", devotion: EGods.NURGLE, description: "If ever Stunned, a successful Toughness Test allows the character to ignore the effects." },
    "Iron Discipline": { name: "Iron Discipline", tier: 2, prerequisites: "Fel 30", devotion: EGods.SLAANESH, description: "If the character is visible to his followers, either in person or via vox- or pict-caster, they may reroll failed Willpower Tests made to resist Fear and Pinning. Heretics cannot beneﬁt from the Iron Discipline of another Heretic." },
    "Jaded": { name: "Jaded", tier: 1, prerequisites: "WP 40", devotion: EGods.NURGLE, description: "Dead bodies, xenos creatures, and other terrifying but natural occurrences do not elicit Fear Tests. Terrors of the Warp still affect the character normally." },
    "Killing Strike": { name: "Killing Strike", tier: 2, prerequisites: "WS 50", devotion: EGods.KHORNE, description: "When making an All Out Attack, the character may spend one Infamy Point to choose to make that melee attack unavoidable with the Parry or Dodge Skills. Other means of stopping attacks (such as Shadow Fields or Displacement Fields) are unaffected." },
    "Leap Up": { name: "Leap Up", tier: 1, prerequisites: "Ag 30", devotion: EGods.SLAANESH, description: "The character may stand up as a Free Action" },
    "Legion Weapon Training": { name: "Legion Weapon Training", tier: 1, prerequisites: "Chaos Space Marine", devotion: EGods.UNALIGNED, description: "Chaos Space Marines are proﬁcient with all weapons of the Bolt, Chain, Melta, Plasma, Power, Flame, Primary, Las, Solid Projectile, Thrown, and Launcher categories. In essence the only weapons that a Chaos Space Marine is not proﬁcient with are Exotic Weapons; for these he must acquire individual Talents as normal." },
    "Lesser Minion of Chaos": { name: "Lesser Minion of Chaos", tier: 1, prerequisites: "Fel 40", devotion: EGods.UNALIGNED, description: "Details on creating a Lesser Minion can be found on page 135. This Talent may be taken more than once, each time adding another Lesser Minion to the character’s retinue." },
    "Light Sleeper": { name: "Light Sleeper", tier: 1, prerequisites: "Per 30", devotion: EGods.TZEENTCH, description: "The character is always assumed to be awake, even when asleep, for the purposes of Awareness Tests or surprise. Unfortunately, the character’s sleep is not deep and can be frequently interrupted, resulting in a less-than-cheery disposition when awake." },
    "Lightning Attack": { name: "Lightning Attack", tier: 3, prerequisites: "Swift Attack", devotion: EGods.KHORNE, description: "A Heretic with this Talent may make the Lightning Attack Combat Action (see page 238)." },
    "Lightning Reflexes": { name: "Lightning Reflexes", tier: 1, devotion: EGods.SLAANESH, description: "When making an Initiative Roll the character may roll twice and add the highest of the two dice result to his Agility Bonus." },
    "Lumimen Shock": { name: "Lumimen Shock", tier: 2, prerequisites: "Luminen Capacitors, Mechanicus Implants", devotion: EGods.UNALIGNED, description: "By making a successful melee attack (without penalties for being unarmed), the Heretic may deal 1d10 plus his Willpower Bonus in Energy Damage with Pen 2 and the Shocking Quality." },
    "Luminen Blast": { name: "Luminen Blast", tier: 3, prerequisites: "Luminen Shock", devotion: EGods.UNALIGNED, description: "The energies charged in the Heretic’s Luminen Capacitors can be expelled in a ranged attack with a Ballistic Skill Test directing this energy against a single target within 10 metres inﬂicting 1d10 plus twice the character’s Willpower Bonus in Energy Damage with Pen 2 and the Shocking and Blast (X) Quality. X equals any value between 1 and the Heretic’s Intelligence Bonus, chosen by the Heretic at the time of ﬁring. The attack uses a Half Action to accomplish (counting as a Single Attack Action) and the character must pass a Toughness Test when he uses this ability or gain a level of Fatigue." },
    "Maglev Transcendence": { name: "Maglev Transcendence", tier: 2, prerequisites: "Maglev Coils/Mechanicus Impants", devotion: EGods.UNALIGNED, description: "He may hover for a number of minutes equal to 1d10 plus twice his Toughness bonus. He may move his Run speed when making a Half Move Action, and suffers no Damage from falling if the Coils are active. Each use expends half the power stored in the coils (therefore he may use the Coils twice before recharging them)." },
    "Marksman": { name: "Marksman", tier: 2, prerequisites: "BS 35", devotion: EGods.UNALIGNED, description: "Distance is no protection against the character’s marksmanship and he suffers no penalties for Ballistic Skill Tests at long or extended range." },
    "Master Chirurgeon": { name: "Master Chirurgeon", tier: 3, prerequisites: "Medicae +10", devotion: EGods.NURGLE, description: "The character gains a +10 bonus on all Medicae Skill Tests. Any successful heal attempt (including Heavily and Critically wounded patients) by the character heals 2 additional Wounds. If his patient is in danger of losing a limb from a Critical Hit (see Chapter VII: Combat), the character provides the patient with a +20 bonus to the Toughness Test to prevent limb loss." },
    "Master Engineer": { name: "Master Engineer", tier: 3, prerequisites: "Tech Use +20, Mechanicus Implants", devotion: EGods.UNALIGNED, description: "The character gains a +10 bonus on Tech-Use Skill Tests. The Heretic may spend an Infamy Point to automatically succeed on a Tech-Use Skill Test for enhancement, repair, or upgrade of a machine’s system, taking the minimum amount of time possible on the task. In addition at the GM’s discretion the character may repair objects without all the parts at hand, jury-rigging a working device as good as the original." },
    "Mastery": { name: "Mastery", tier: 3, prerequisites: "Rank 4 in selected Skill", devotion: EGods.UNALIGNED, description: "The character may spend an Infamy Point to automatically pass a Skill Test with his chosen skill provided the ﬁnal modiﬁer to his Skill Test is 0 or greater. In addition to simply passing the test the character passes it by a number of Degrees of Success equal to the Characteristic Bonus of its associated Characteristic. This Talent may be taken more than once, each time for a different Skill." },
    "Mechadendrite Use": { name: "Mechadendrite Use", tier: 2, prerequisites: "Mechanicus Implants", devotion: EGods.UNALIGNED, description: "The character is trained in the use of a particular kind of Mechendrite (Weapon or Utility) See gear/cybernetics." },
    "Meditation": { name: "Meditation", tier: 1, devotion: EGods.TZEENTCH, description: "10 minutes without interruption results in a success on a Willpower Test and removes one level of Fatigue." },
    "Mighty Shot": { name: "Mighty Shot", tier: 3, prerequisites: "BS 40", devotion: EGods.NURGLE, description: "The character adds half his Ballistic Skill Bonus to Damage the character inﬂict with a ranged weapon." },
    "Mimic": { name: "Mimic", tier: 1, devotion: EGods.SLAANESH, description: "The character must study the voice patterns of his intended target for at least one hour for proper imitation, and speak the same language. The character cannot accurately copy the voice of a xenos due to the difference in physiology and the subtle complexities of most alien languages. Listeners must pass a Scrutiny Skill Test with a –10 penalty to penetrate the deception. If the character’s study relied on vox recordings, or comm-link conversations rather than in-person observation, then there is no penalty to the Scrutiny Skill Test to see through it. The character’s deception automatically fails if the listener can clearly see him." },
    "Minion of Chaos": { name: "Minion of Chaos", tier: 2, prerequisites: "Fel 45, Infamy 30", devotion: EGods.UNALIGNED, description: "Details on creating a Minion can be found on page 135. This talent may be taken more than once, each time adding another Minion to the character’s retinue." },
    "Nerves of Steel": { name: "Nerves of Steel", tier: 2, devotion: EGods.NURGLE, description: "The character may reroll failed Willpower Tests to avoid or recover from Pinning. In addition is seldom impressed by the aggressive displays of his enemies and gains a +10 to Willpower Tests when resisting the effects of enemy Intimidation Skill attempts." },
    "Never Die": { name: "Never Die", tier: 3, prerequisites: "WP 50, T 50", devotion: EGods.NURGLE, description: "The character can spend one Infamy Point to ignore the effects of injury, Fatigue, and Stunning for a single combat. This talent does not prevent the Damage, but allows the character to temporarily ignore its effects for the duration of the combat. Death still affects him normally." },
    "Orthoproxy": { name: "Orthoproxy", tier: 1, devotion: EGods.UNALIGNED, description: "The character receives a +20 bonus to Willpower Tests made to resist mind control or interrogation. He tends not to get jokes, however." },
    "Paranoia": { name: "Paranoia", tier: 2, devotion: EGods.TZEENTCH, description: "The character gains a +2 bonus on Initiative rolls, and the GM may secretly test using the character’s Perception to notice hidden threats. The price of this eternal vigilance is a twitchy disposition and the inability to relax." },
    "Peer": { name: "Peer", tier: 1, prerequisites: "Fel 30", devotion: EGods.UNALIGNED, description: "The character knows how to deal with a particular social group or organisation. The character gains a +10 bonus to all Fellowship Tests when interacting with this chosen group and at the GM’s discretion my call upon them for favours from time to time.  In game terms, the bonus to Fellowship Tests increases to +10 times X, with X being the number of times the Talent has been awarded." },
    "Pity the Weak": { name: "Pity the Weak", tier: 1, prerequisites: "S 35, WP 35", devotion: EGods.KHORNE, description: "The character has nothing but contempt for those less powerful than himself and gains a +10 on Command, Commerce, Deception, and Intimidation Skill Tests when opposed by someone with either a Strength or Willpower Characteristic lower than themselves. Unfortunately, the character also has trouble hiding his contempt from such people and suffers a –10 to Charm Skill Tests when dealing the same people." },
    "Polyglot": { name: "Polyglot", tier: 1, prerequisites: "Int 40, Fel 30", devotion: EGods.TZEENTCH, description: "The character can make untrained Skill Tests with the Linguistics Skill even though it is a Specialist Skill. Being a Polyglot is not the same as having a true knowledge of a language, and characters must make a Linguistics Skill Test (along with the –20 modiﬁer for being untrained) to have more than the most basic conversations or exchanges of ideas." },
    "Precise Blow": { name: "Precise Blow", tier: 2, prerequisites: "WS 40, Sure Strike", devotion: EGods.SLAANESH, description: "When making a Called Shot (see page 234) melee attack, the character reduces the penalty by 10 (so a –20 becomes a –10 instead). If combined with Sure Strike, this reduces the penalty to 0." },
    "Preternatural Speed": { name: "Preternatural Speed", tier: 3, prerequisites: "WS 40, Ag 40", devotion: EGods.SLAANESH, description: "When making a Charge Move the character doubles his normal charge movement (i.e. a character with an AB of 3 has a charge movement of 9 metres, which is then doubled to 18 metres by the Preternatural Speed Talent)." },
    "Prosanguine": { name: "Prosanguine", tier: 2, prerequisites: "Autosanguine", devotion: EGods.NURGLE, description: "He must spend 10 minutes in mediation and make a Tech-Use Test. If he succeeds, he heals 1d5 points of Damage plus one additional point of Damage for every two Degrees of Success. If he rolls a 96 or higher, he loses the ability to use his Black Blood implants or the Prosanguine Talent for one week." },
    "Psy Rating": { name: "Psy Rating", tier: 3, prerequisites: "Psyker", devotion: EGods.UNALIGNED, description: "Grants +1 Psy Rating to a maximum of 10" },
    "Psychic Power": { name: "Psychic Power", tier: 1, prerequisites: "Psyker", devotion: EGods.SPECIAL, description: "Either through training or natural development, the character has learned an additional Psychic Power. This power must be selected from one of the powers presented in Chapter VI: Psychic Powers. The experience cost and requirements for psychic powers are not the same as other Talents (as detailed on page 77) and can instead be found in Chapter VI: Psychic Powers. Characters must have the Psyker trait to take this talent." },
    "Quick Draw": { name: "Quick Draw", tier: 1, devotion: EGods.UNALIGNED, description: "The character can draw and ready a weapon as a Free Action when the character is armed with a Pistol or Basic-class ranged weapon, or a melee weapon that can be wielded in one hand." },
    "Radiant Presence": { name: "Radiant Presence", tier: 1, prerequisites: "Fel", devotion: EGods.SLAANESH, description: "Everyone within 20 metres that can see the character feels a touch of euphoria just by being near him and suffer lessened effects from things that would upset of distress them. Those affected gain a +10 to Willpower Tests to resist Fear or against Intimidation attempts. This ability affects all those close to the character indiscriminately, though not the character himself." },
    "Rapid Reaction": { name: "Rapid Reaction", tier: 2, prerequisites: "Ag 40", devotion: EGods.SLAANESH, description: "When surprised or ambushed, a successful Agility Test allows the character to act normally." },
    "Rapid Reload": { name: "Rapid Reload", tier: 1, devotion: EGods.SLAANESH, description: "The character halves all reload times, rounding down. Thus, Half Action reload become a Free Action, a Full Action reload becomes a Half Action and so on." },
    "Raptor": { name: "Raptor", tier: 1, prerequisites: "Operate (Aeronautica)", devotion: EGods.UNALIGNED, description: "When the character makes a Charge Action using a Jump Pack his melee attack deals an extra d10 of Damage for every two Degrees of Success he scores on his Weapon Skill Test to a maximum of two extra d10." },
    "Resistance": { name: "Resistance", tier: 1, devotion: EGods.NURGLE, description: "Each time the character select this talent, choose one area of resistance. The character gains a +10 bonus when making tests to resist the effects of this group. The character’s GM may wish to approve certain choices or have them justified by the character’s past." },
    "Sacrifice": { name: "Sacrifice", tier: 2, prerequisites: "Psy Rating", devotion: EGods.UNALIGNED, description: "A psyker with the Sacriﬁce Talent may ritually prepare and slay a sacriﬁcial victim (which must be a human or intelligent xenos). It takes a Forbidden Lore (Warp) Skill Test, taking 2d5 minutes to prepare and anoint a bound and helpless sacrificial victim. Once prepared, it takes a Full Round action to slay the victim. In the subsequent turn, the psyker gains a +5 bonus to his Focus Power Test for every Degree of Success scored on Forbidden Lore (Warp) Skill Test. In addition, should any Psychic Phenomena or Perils of the Warp occur, the effects are focused through the dying victim. Any effects that would normally affect the psyker instead cause the body to burn to ash in a blaze of iridescent warp-flame, dealing 1d10 Energy Damage to anyone within three metres of the victim (gaining the Warp Weapon trait and increasing the area to a ﬁve-metre radius if it was caused by Perils of the Warp)." },
    "Sharpshooter": { name: "Sharpshooter", tier: 2, prerequisites: "BS 40, Deadaye Shot", devotion: EGods.UNALIGNED, description: "When making a Called Shot (see page 234), the character reduces the penalty by 10 (so a –20 becomes a –10 instead). If combined with Deadeye shot, this reduces the penalty to 0." },
    "Sidearm": { name: "Sidearm", tier: 3, prerequisites: "WS 40, BS 40, Two-Weapon Wielder (Ballistic, Melee)", devotion: EGods.UNALIGNED, description: "When armed with a pistol and melee weapon in either hand (which the character could normally use single-handedly), the character reduces the penalty for Two-Weapon Fighting by 10 (so –20 would drop to –10). If taken with the Ambidextrous Talent, this drops the penalty to +0." },
    "Sound Constitution": { name: "Sound Constitution", tier: 1, devotion: EGods.NURGLE, description: "The character gains an additional Wound.  The character may purchase this Talent multiple times up to his Toughness Bonus." },
    "Sprint": { name: "Sprint", tier: 3, devotion: EGods.SLAANESH, description: "When taking a Full Move Action, the character can move an extra number of metres equal to his Agility Bonus. When taking the run action, the character may double his movement for one round. The character gains one level of Fatigue if he uses this Talent two turns in a row." },
    "Step Aside": { name: "Step Aside", tier: 3, prerequisites: "Ag 40, Dodge, Parry", devotion: EGods.UNALIGNED, description: "The character may make an additional Evasion attempt (either a dodge or a parry) once per round. In effect, this gives the character a second Reaction that may only be used for Dodge or Parry attempts, allowing two dodges, two parries or a dodge and a parry in a turn. However, the character may still only attempt a single dodge or parry against each individual attack." },
    "Storm of Iron": { name: "Storm of Iron", tier: 2, prerequisites: "BS 45, Weapon Training (any ranged)", devotion: EGods.KHORNE, description: "When the Heretic deals Damage to a Horde with a semi-auto burst, a full-auto burst, or a weapon with the Spray or Blast Qualities, the Heretic deals additional Damage to the Horde equal to half his Ballistic Skill Bonus. This additional Damage is applied once per attack, not once per hit." },
    "Street Fighting": { name: "Street Fighting", tier: 1, prerequisites: "WS 30", devotion: EGods.KHORNE, description: "When armed with a knife (powered, chain or otherwise), similar small weapon, or simply armed with his bare hands, he adds his Weapon Skill Bonus to any Critical Damage he inflicts." },
    "Strong Minded": { name: "Strong Minded", tier: 2, prerequisites: "WP 30, Resistance (Psychic Techniques)", devotion: EGods.TZEENTCH, description: "He may reroll failed Willpower Tests to resist any Psychic Powers that affect his mind. Psychic Powers that have a physical effect, such as Doombolt, are unaffected by this Talent." },
    "Sure Strike": { name: "Sure Strike", tier: 1, prerequisites: "WS 30", devotion: EGods.SLAANESH, description: "When making a Called Shot (see page 234) melee attack, the character reduces the penalty by 10 (so a – 20 becomes a –10 instead)." },
    "Swift Attack": { name: "Swift Attack", tier: 2, prerequisites: "WS 30", devotion: EGods.KHORNE, description: "When a character makes a melee attack, he scores one hit on the initial Degree of Success on his Weapon Skill Test, plus one additional hit for every two additional Degrees of Success. Additional hits are counted as separate hits for all intents and purposes, roll their Damage separately and are reduced by Armour and Toughness as normal. Additional hits are made against the same body location as the original hit. When using Dodge or Parry against a Swift Attack then each Degree of Success scored with a Dodge or Parry Skill Test negates one hit from the Swift Attack. If a character is fi ghting with a weapon in either hand or benefits from a Talent or Power which allows him to make more than one attack in his turn only one of his attacks may be a Lightning Attack and have the chance of scoring additional hits. Melee weapons with the Unwieldy Special Quality cannot be used to make Lightning Attacks" },
    "Takedown": { name: "Takedown", tier: 1, devotion: EGods.UNALIGNED, description: "As a Half Action or when making a Charge Attack the character may declare that he is attempting a takedown against an opponent in melee combat. He then roll to hit (using his Weapon Skill) as normal; using any modiﬁers for weapons and talents (or the +20 bonus from Charging). If the character hits and would have done at least one point of Damage (after reduction for Armour and Toughness) it is ignored, but the character’s opponent must make a Challenging (+0) Toughness Test or be stunned for 1 round and knocked prone. In addition, when performing a Stun Action, the character does not suffer a –20 penalty to his Weapon Skill." },
    "Target Selection": { name: "Target Selection", tier: 3, prerequisites: "BS 30", devotion: EGods.UNALIGNED, description: "The character may shoot into melee with no penalty. If he aims beforehand, he can avoid any chance of hitting friendly targets." },
    "Technical Knock": { name: "Technical Knock", tier: 1, prerequisites: "Int 30", devotion: EGods.UNALIGNED, description: "The character may un-jam any gun as a Half Action, but may only use this talent on one weapon per round. The character must touch the weapon (often quickly and violently) to make this talent work." },
    "Thunder Charge": { name: "Thunder Charge", tier: 3, prerequisites: "S 50", devotion: EGods.KHORNE, description: "When the character makes a Charge Action he can barrel through foes to get to his target. Make an Opposed Strength Test between the character and each foe in his way (i.e., each foe his charge would take him past or through). Those that fail are knocked to the ground. Once the character has tested to knock down those in his way, he may complete his Charge and make his attacks against his intended target." },
    "Total Recall": { name: "Total Recall", tier: 1, prerequisites: "Int 30", devotion: EGods.TZEENTCH, description: "The character can automatically remember trivial facts or pieces of information the character might feasibly have picked up in the past. When dealing with more detailed, complex or obscure facts, such as the exact layout of a defence network, the GM may require a successful Intelligence Test to recall the information." },
    "True Grit": { name: "True Grit", tier: 3, prerequisites: "T 40", devotion: EGods.NURGLE, description: "Whenever the character suffers Critical Damage (after reduction for Armour and Toughness) reduce the amount by his Toughness Bonus (to a minimum of 1)." },
    "Two-Weapon Wielder": { name: "Two-Weapon Wielder", tier: 2, devotion: EGods.UNALIGNED, description: "Allows the character to use a weapon in each hand. This allow the character to attack twice in a round, once from each weapon. Both tests suffer a -20 penalty. Taken once for melee or range weapons. If both types are acquired he may fight with ine melee and one ranged weapon. " },
    "Unarmed Master": { name: "Unarmed Master", tier: 2, prerequisites: "WS 45, Ag 40, Unarmed Warrior", devotion: EGods.KHORNE, description: "He counts as having the Deadly Natural Weapon Trait." },
    "Unarmed Warrior": { name: "Unarmed Warrior", tier: 1, prerequisites: "WS 35, Ag 35", devotion: EGods.KHORNE, description: "Due to extensive training in techniques of unarmed combat the character has gained the Natural Weapon Trait (see page 142) improving all his unarmed attacks from 1d5-3 to 1d10 Primitive (7) and meaning he counts as armed even when facing opponents with weapons while bare handed." },
    "Unholy Devotion": { name: "Unholy Devotion", tier: 3, prerequisites: "Minion of Chaos", devotion: EGods.UNALIGNED, description: "The character’s Minions are fanatically devoted to him and would gladly die in his name. Whenever the character would be hit by a ranged attack he can make an Agility Test for any of his Minions which are no further away from him than his Agility Bonus in metres. If he passes the test then they suffer the hit instead as they have thrown themselves in front of their master, and all the hits effects are worked out against the Minion." },
    "Unremarkable": { name: "Unremarkable", tier: 1, devotion: EGods.SLAANESH, description: "Any attempts to recall the character’s face or spot him in a crowd suffer a –20 penalty. In addition the character gains a +20 when using the Deceive Skill to either convince someone he has never seen or met him before or if he is trying to appear harmless and ordinary. Naturally this Talent doesn’t work if the character wears distinctive clothes or armour, or carries unique weaponry—a Chaos Space Marine hiding in a crowd of Imperial citizens is pretty easy to spot, especially since they are probably running screaming in all directions…" },
    "Unshakable Will": { name: "Unshakable Will", tier: 2, prerequisites: "WP 35", devotion: EGods.NURGLE, description: "The character may reroll any failed Willpower Tests to avoid the effects of Fear." },
    "War Cry": { name: "War Cry", tier: 3, prerequisites: "S 50, WP 50, Disturbing Voice", devotion: EGods.NURGLE, description: "The character may declare at the start of any of his turns that he is making a war cry as a Free Action. All creatures within a 50-metre radius treat the character as if he had a Fear Rating of 1 provided they can hear him (though they do not need to be able to see him) until the start of his next turn. While screaming his bloodcurdling war cry, the character may not communicate in any other way. Making a war cry is taxing however and at the end of any encounter in which it is used (even if only for a single turn) the character suffers a level of Fatigue." },
    "Warp Conduit": { name: "Warp Conduit", tier: 2, prerequisites: "Psy Rating, Strong Minded, WP 50", devotion: EGods.TZEENTCH, description: "When Pushing (see page 207), the character may spend one Infamy Point to add 1d5 to his Psy Rating. Channelling such vast amounts of power is dangerous, however, and he must add +30 to any resultant Psychic Phenomenon rolls." },
    "Warp Lock": { name: "Warp Lock", tier: 3, prerequisites: "Psy Rating, Strong Minded, WP 50", devotion: EGods.TZEENTCH, description: "Once per game session the character may ignore a Psychic Phenomenon result he has rolled (including a result which would indicate rolling on the Perils of the Warp Table) completely negating its effects. Such rapid dislocation from the warp is stressful and traumatic to the character’s mind, however, and he suffers 1d5 Damage (not reduced by armour or Toughness) as a result, and may not make any Focus Power Tests or sustain other Psychic Powers until the beginning of his next Turn." },
    "Warp Sense": { name: "Warp Sense", tier: 1, prerequisites: "Psy Rating, Psyniscience, Per 30", devotion: EGods.TZEENTCH, description: "After gaining this talent, using the Psyniscience Skill requires a Free Action instead of a Half Action. In addition, the character can passively detect psychic effects and entities without the need to actively seek them out. Whenever the character could detect such warp signature or creature the GM may allow him to make a Psyniscience Skill Test to detect it, in the same way he might use Awareness to spot an ambush without knowing it is there." },
    "Weapon Training": { name: "Weapon Training", tier: 1, devotion: EGods.UNALIGNED, description: "The character can use all weapons with Class: Pistol, Basic, Heavy, Throwing, and Melee within the group he has selected with this talent. When a character attempts to use a weapon he does not have the correct Weapon Training Talent for, he suffers a –20 penalty to any relevant Weapon Skill or Ballistic Skill Test. This talent may be taken more than once, each time with a different specialisation." },
    "Weapon Tech": { name: "Weapon Tech", tier: 1, prerequisites: "Tech Use +10, Int 40", devotion: EGods.UNALIGNED, description: "Once per combat, the character can enhance his weapon increasing it ﬁrepower and deadliness. For one Round per combat Encounter, a weapon personally wielded by the character from the Melta, Plasma, Power, or Exotic category increases its Damage and Penetration by an amount equal to his Intelligence Bonus." },
    "Whirlwind of Death": { name: "Whirlwind of Death", tier: 2, prerequisites: "WS 40", devotion: EGods.UNALIGNED, description: "When attacking a Horde, the character deals additional Magnitude damage equal to half his Weapon Skill Bonus." },
    "Wisdom of the Ancients": { name: "Wisdom of the Ancients", tier: 2, prerequisites: "Int 40", devotion: EGods.TZEENTCH, description: "The character may spend an Infamy Point to get insight on a situation and ask the GM a question (which he should answer as helpfully as possible) about the immediate situation which his character faces. The question can be anything the character might legitimately know from stories he has heard or his own experience. For example, when raiding an Imperial cruiser the question might be “where are the Navigator’s quarters.”" },

    "Amphibious": {name: "Amphibious", tier: 0, description: "The creature can breathe water as well as air and can stay underwater indefi nitely without the need to surface. If this trait is taken by a creature with the ability to breathe a substance other than air (i.e. water, ammonia, carbon-dioxide etc) then it can breathe air as well as its native atmosphere."},
    "Amorphous": {name: "Amorphous", tier: 0, description: "An amorphous creature which has a malleable form, capable of squeezing its body or expanding it. It can change size by one-step in either direction, though doing so does not change its speed. Creatures with this trait determine their movement rates using one-half their Agility Bonus rounded up. Amorphous creatures are often missing senses and so rely on the Unnatural Senses Trait to perceive their surroundings."},
    "Auto-Stabilised": {name: "Auto-Stabilised", tier: 0, description: "The creature always counts as braced when firing heavy weapons and therefore may fire a ranged weapon on full or semi-automatic without taking the Brace action first without suffering any penalties to hit."},
    "Bestial": {name: "Bestial", tier: 0, description: "A creature with this trait is animalistic, tending to behave instinctively rather than rationally. No matter how high its Intelligence score is, it is still bestial. A creature with this trait never needs to make a Survival Skill Test whilst in its natural habitat. Unless starving or desperate, a bestial creature must make a Willpower Test when frightened, startled or injured. On a failed test, the creature flees."},
    "Blind": {name: "Blind", tier: 0, description: "Blind creatures automatically fail all tests based on vision and automatically fail all Ballistic Skill Tests. Such creatures take a –30 penalty to Weapon Skill Tests and most other tests that ordinarily involve or are benefited by vision. Note that this Trait indicates not only that the creature cannot see but that it also has no other senses which would provide the same benefit as eyes. The Blind Fighting Talent reduces the penalties to Weapon Skill Tests as normal for Blind creatures, reducing it from –30 to –15."},
    "Brutal Charge": {name: "Brutal Charge", tier: 0, description: "A creature with this trait deals an extra (X) points of damage per attack when it charges in the same round."},
    "Burrower": {name: "Burrower", tier: 0, description: "A creature with this trait can move through solid objects by burrowing through them. The number in parenthesis (X) indicates its speed when burrowing. Such creatures can burrow through soil, rock, sand, and so on, though some (expressed in the creature entry) can burrow through metal. When a creature burrows, it leaves behind a tunnel. Creatures of one-size smaller than the creature that created the tunnel can enter these areas without impediment. There’s a 50% chance each round that the tunnel collapses behind the creature."},
    "Crawler": {name: "Crawler", tier: 0, description: "Worms, serpents, and similar creatures crawl and slither rather than walk. The base move for a creature with this trait is half their normal movement rate (round up) as determined by their Agility Bonus, but they do not take penalties for moving over difficult terrain."},
    "Daemonic": {name: "Daemonic", tier: 0, description: "Daemonic creatures are creatures of the warp, but can manifest in real space either through sheer strength of will, hunger, or occult rituals. Creatures with this trait increase their Toughness Bonus against all Damage by the number indicated in the parenthesis (X) in the same manner as an Unnatural Characteristic (see Unnatural Characteristics trait on page 144). The effects of this trait stack with the effects of the Unnatural Characteristic trait. This increase is negated by Damage inflicted by force weapons, psychic powers, holy attacks, or other creatures with this trait. Daemonic creatures are also immune to poison and disease. Creatures with the Daemonic trait also gain benefits when using Psychic Powers as detailed in Chapter VI: The Powers of the Warp."},
    "Dark-Sight": {name: "Dark-Sight", tier: 0, description: "A creature with this trait sees normally even in areas of total darkness, and never takes a penalty for fighting in areas of dim or no lighting."},
    "Fear": {name: "Fear", tier: 0, description: "A creature with this trait has an unnerving appearance. The fear of it always has an associated rating (1–4) to reflect just how awful the creature is to behold and is indicated by the number in parenthesis (X). The higher the Fear Rating the harder it is for others to resist going encounter, as shown on Table 4-7: Fear Ratings. When a character encounters a creature with this Trait, they must make a Willpower Test, modified by the severity of the creature’s trait. On a success, the character may act as normal. On a failure, the character must roll on Table 8-4: Shock (see page 277), adding +10 to the roll for every Degree of Failure by which the Willpower Test was failed."},
    "Flyer": {name: "Flyer", tier: 0, description: "A creature with this trait has the natural ability to fly, whether with broad leathery wings, strange Warp energies, or inflatable gas sacs. The number in parenthesis (X) indicates its speed when flying. This number replaces the creature’s Agility Bonus for movement Actions. For more details on flying, see Chapter I: Playing the Game."},
    "From Beyond": {name: "From Beyond", tier: 0, description: "The mind of a creature with this trait is beyond the petty frailties and precarious sanity of a mortal mind. It is immune to Fear, Pinning, Insanity Points, and psychic powers used to cloud, control, or delude its minds."},
    "Deadly Natural Weapons": {name: "Deadly Natural Weapons", tier: 0, description: "This creature’s natural attacks are powerful enough to crush plasteel or punch through armour. The creature gains all the benefits of the Natural Weapons Trait, except that its attacks no longer have the Primitive Special Quality."},
    "Hoverer": {name: "Hoverer", tier: 0, description: "Creatures with this trait have a limited capability for flight, flying no higher than two metres. The number in parenthesis (X) indicates its speed when hovering. This number replaces its Agility Bonus for purposes of calculating Movement. For more details on flying, see Chapter I: Playing the Game."},
    "Incorporeal": {name: "Incorporeal", tier: 0, description: "Incorporeal creatures are insubstantial and weightless, able to pass through solid objects such as walls and weapons. An incorporeal creature gains a +30 bonus on Stealth Tests due to their ability to merge with objects and the fact they make practically no sounds when they move. Incorporeal creatures gain the Hoverer (6) trait. An incorporeal creature is also immune to normal weapons—they simply pass through its body as if it wasn’t there. Daemons, psychic powers, warp creatures, other incorporeal creatures, and opponents armed with force weapons may all injure an incorporeal creature normally. An incorporeal creature can’t normally affect the mortal world, and thus can’t Damage non-incorporeal opponents unless it has a suitable special ability or Talent. Creatures with the Incorporeal trait can Damage other creatures with the Incorporeal trait normally. Certain devices can keep an incorporeal creature at bay, most notably the Gellar Field of a spaceship. Creatures with this trait cannot pass through such a field."},
    "Machine": {name: "Machine", tier: 0, description: "A creature with the Machine trait is fashioned from inorganic materials and is generally more rigorous then fleshy folks. Machines do not breathe, are immune to the effects of a vacuum, extremes of cold, any mind-influencing psychic effect, and their Armour Points apply toward fire Damage. Machines have a certain number of Armour Points (indicated by the number in parentheses). This armour stacks with worn armour, but not with the Natural Armour Trait."},
    "Multiple Arms": {name: "Multiple Arms", tier: 0, description: "This creature has more than one pair of arms. The exact number of arms is indicated in parenthesis (X), so a Genestealer with Multiple Arms (4) has four arms total. It gains a +10 bonus on Athletics Tests involving climbing and swimming. In addition, for each pair of arms it has—the Multiple Arms value divided by 2—it may make an additional attack as if wielding multiple melee weapons (the creature must either have natural weapons or actual melee weapons to wield for this to take effect). Note, that a creature still requires Two Weapon Wielder (Melee) to make multiple attacks in the same turn."},
    "Natural Armor": {name: "Natural Armor", tier: 0, description: "This creature has a naturally tough hide or an exoskeleton, affording it some protection against attacks. The number in parenthesis (X) indicates how many Armour Points the creature gains to all locations. This trait does not stack with armour. This armour stacks with worn armour, but not with the Machine Trait."},
    "Natural Weapons": {name: "Natural Weapons", tier: 0, description: "This creature has sharp claws, teeth, spines or some other natural weapon with which it can slice and dice its foes. It counts as being armed even when not wielding weapons. Its attacks deal 1d10 points of Damage plus its Strength Bonus. The creature may make attacks with its Natural Weapons using its Weapon Skill, though it cannot parry with its natural weapons, nor can it be disarmed—unless, of course its limbs are hacked off ! (In general, creatures with Natural Weapons are immune to the Power Field quality, as it’s assumed they have enough teeth, claws, or spines to ignore a few being removed.) Natural weapons have the Primitive (7) Special Quality, unless stated otherwise in the profile."},
    "Phase": {name: "Phase", tier: 0, description: "A creature with this trait can transform its body into an insubstantial state, passing ghost-like through solid objects or barriers of any sort. This trait functions exactly like the Incorporeal trait, except the creature may become solid or insubstantial by spending a Half Action. Phase carries additional limitations. A creature in a phased state cannot cross psychically charged barriers, holy wards, or energy fields designed to flux space or manipulate the warp, such as a Gellar Field or a Void Shield."},
    "Psyker": {name: "Psyker", tier: 0, description: "Whether due to unnatural influences, the study of forbidden lore, or simply sheer force of will, this individual is a Psyker, and can bend the Immaterium to his will. Though this Trait has no benefits on its own, it allows characters to purchase the Psyniscience Skill and the Psy Rating and Psychic Power Talents."},
    "Possession": {name: "Possession", tier: 0, description: "Certain spirit-beings and denizens of the warp have the ability to possess mortal bodies. This is a far more insidious and damaging process than most forms of mental control, or even the power of the strongest psykers to “wear” the bodies of others, as the very fabric of the assaulting warp being is merged with the victim’s living body on a fundamental level so as to allow it to remain in our reality. The mechanisms by which these possessions occur in the game are detailed below, although circumstance and the nature of the entity itself may vary. Note that Daemonhosts, being extraordinary powerful examples of deliberate, ritualistic possessions, designed to bind and control daemons in living vessels, are a special case and dealt with separately."},
    "Quadruped": {name: "Quadruped", tier: 0, description: "Quadrupeds are much quicker over a distance than their bipedal counterparts. There movement is double that normally indicated by their Agility Bonus (see page 39). This trait is intended to cover creatures with hind legs and forepaws. Creatures with more than four legs may gain this Trait as well increasing their movement as indicated above but adding an addition 2 to their final movement rate (after doubling) for each set of legs beyond the first two (i.e. +2 for 6 legs, +4 for 8 legs etc)."},
    "Regeneration": {name: "Regeneration", tier: 0, description: "Creatures with this trait heal very quickly. Each round, at the start of its turn, the creature makes a Toughness Test to removes an amount of Damage indicated in the parenthesis after the trait. It loses this trait when slain."},
    "Size": {name: "Size", tier: 0, description: "Creatures come in one of ten different size categories, as shown on Table 4-8: Size. Size affects movement, how well the creature can hide and move undetected and how easy or hard it is to strike in combat. When calculating movement, apply the size modifier first, and then other modifiers from other Traits or Talents (such as Quadruped). For the purposes of comparison, a human is a Size (4) creature. Note, a creature’s movement cannot be reduced to less than 1."},
    "Sonar Sense": {name: "Sonar Sense", tier: 0, description: "A creature with this trait perceives its surroundings by emitting a frequency noise, allowing the creature to locate the position of any solid object within 30 metres. Other creatures within this 30 metre range, and that succeed on an Awareness Skill Test at -10, may detect the curious keening."},
    "Soul Bound": {name: "Soul Bound", tier: 0, description: "The soul of this creature is bound to a higher power or being, in return for a measure of protection. When this Trait is fi rst gained, the entity to which the soul is bound must be decided. Many Imperial psykers, especially Astropaths, are soul-bound to the Emperor, for example, whereas Chaos Sorcerers may be bound to one of the Ruinous Powers. A soul-bound psyker rolls an additional die when forced to roll on the Perils of the Warp table, discarding whichever one he chooses to get a more favourable result. (Essentially, he may replace either the ones die or the tens die with the extra die he rolls) Upon becoming soul-bound, a character must choose one of the following effects: 1d10 Insanity Points (this does not apply to Heretics, as they do not suffer Insanity), the permanent loss of their sight, permanent loss of 1d10 from one Characteristic, or a random mutation. In addition, a soul-bound character is permanently indebted to the entity, which undoubtedly entails all manner of other duties and consequences."},
    "Stampede": {name: "Stampede", tier: 0, description: "Whenever a creature with this trait fails a Willpower Test, it automatically stampedes, charging in a straight line forwards as far as possible, overrunning anything in its path until the source of danger has escaped or is crushed under hoof, taking Damage equal to the creature’s Natural Weapon Damage (or 1d5+SB I if it doesn’t have one). One stampeding creature automatically causes all other creatures within sight to stampede. The stampede lasts until the source of danger is no longer visible or for 1d10 minutes, whichever occurs last."},
    "Strange Physiology": {name: "Strange Physiology", tier: 0, description: "A creature with this Trait has an unusual, alien, or altogether bizarre anatomy. All hits count as Body hits and death results when damage equals or exceeds the creature’s Wounds."},
    "Sturdy": {name: "Sturdy", tier: 0, description: "Sturdy creatures are hard to move and thus gain a +20 bonus to tests made to resist grappling and uses of the Takedown Talent."},
    "Swarm": {name: "Swarm", tier: 0, description: "Too small to do much on their own, swarms overwhelm their opponents via sheer weight of numbers. A swarm is often a mass of Miniscule or Puny creatures. Any attack from a weapon that does not either have the Blast, Flame, Spray, or Scatter Qualities only inflicts half Damage. In most circumstances, a swarm creature cannot be Grappled, Knocked Down, or Pinned, and the swarm may “pour” through suitable small openings such as ducts, vents, and the like, but they may not Jump. The swarm is counted as being destroyed once all its Wounds are lost. The swarm’s attacks have a variable Penetration value (roll each time an attack lands), representing its ability to engulf their victims and attack vulnerable areas."},
    "The Stuff of Nightmares": {name: "The Stuff Of Nightmares", tier: 0, description: "Some warp entities are so terribly powerful the will perpetuating their bodies in our reality is almost impossible to break or disrupt. Such entities are completely immune to the effects of poisons, diseases, the need to breathe, most environmental hazards, bleeding, stunning. They also ignore any critical result other than one that would destroy them outright, unless it was caused by a psychic power, force weapon, or holy attack."},
    "Touched by the Fates": {name: "Touched by the Fates", tier: 0, description: "The creature or NPC has a number of Fate Points shown in parenthesis (X). It may use these Fate Points in exactly the same way as Player Characters and may even ‘burn’ a Fate Point to survive death and destruction at the GM’s discretion. The rules for Zealous Hatred also apply to this creature or NPC."},
    "Toxic": {name: "Toxic", tier: 0, description: "A creature with this trait is poisonous Some weapons rely on toxins and poisons to do their damage. Anyone that successfully deals damage to a Toxic creature must make a Toughness Test with a penalty equal to 10 times the number in parenthesis (X) or suffer 1d10 points of Damage (of the same type as the weapon which inflicted the hit) not reduced by armour or Toughness. For example a creature with Toxic (4) would impose a –40 on Toughness Tests. Some creatures may carry additional effects with their toxins or inflict more Damage as indicated in their individual descriptions."},
    "Undying": {name: "Undying", tier: 0, description: "This creature has a strange and ill understood physiology. It cannot be harmed by diseases, poisons, or toxic effects. It has no need to breathe and can survive unaided in a vacuum."},
    "Unnatural Characteristic": {name: "Unnatural Characteristic", tier: 0, description: "One or more of the creature’s Characteristics is unnatural. This is indicated by a number which is then added directly to the creature’s Characteristic Bonus. For example, a creature with a Strength of 41 normally has a 4 Strength Bonus. If the creature had Unnatural Characteristic Strength (3) then its Strength Bonus increases to 7 (4+3). A creature may have this Trait more than once. Each time, it is applied to a new Characteristic. In addition, whenever someone with an Unnatural Characteristic succeeds on a Test utilizing that Characteristic, they gain a number of bonus Degrees of Success equal to half of their Unnatural Characteristic."},
    "Unnatural Senses": {name: "Unnatural Senses", tier: 0, description: "The creature can perceive its surroundings using senses other than sight or hearing, using special organs, fine hairs, or some other disturbing trait to make itself aware of what’s around it. This trait always includes a range in metres indicated by the number in parenthesis (X)"},
    "Warp Instability": {name: "Warp Instability", tier: 0, description: "Most warp-entities have a tenuous hold on our reality, often maintained only by horror and bloodshed, and may be disrupted if sufficient harm can be done to their manifested forms. If a creature with this trait takes Damage and does not deal Damage or Insanity Points on other creatures by the end of its next turn, it must make a Willpower Test. The creature takes 1d5 Damage for failure, plus 1 Damage for each Degree of Failure. This Damage cannot be prevented by any means including armour and Toughness. If this would deal Damage equal to or in excess of the creature’s Wounds, it is cast back into the warp."},
    "Warp Weapons": {name: "Warp Weapons", tier: 0, description: "Creatures with this trait have weapons that are partially insubstantial, able to ignore such mundane things as armour or cover. Natural weapons and attacks made by a creature with this trait ignore physical armour unless it is created from psychoactive materials or is somehow warded against the warp. Force fields still work against these attacks normally"},
} as ITalentState


